import Vue from 'vue'
import Router from 'vue-router'
import Description from "../components/Description";
import Pojie from "../components/Pojie";
import DescView1 from "../components/DescView1";
import Passage from "../components/Passage";
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/'},
    {path: '/desc',component:Description,children:[
        {path:"/view1",component:DescView1}
      ]},
    {path: '/pojie',component:Pojie},
    {path: '/passage',component:Passage},
  ]
})



